define("discette/models/user", 
  ["exports"],
  function(__exports__) {
    "use strict";
    var User = Ember.Object.extend( {
      avatarUrl: function(){
      	return this.get('rootUrl') + this.get('avatar_template').replace(/\{size\}/g, '32');
      }.property()
    });

    __exports__["default"] = User;
  });